gn-items: center;\r\n}\r\n\r\nheader .nav-container .logo img {\r\n  width: 100px;\r\n}\r\n\r\nheader .nav-container .right-nav {\r\n  display: flex;\r\n  padding: 15px 0;\r\n}\r\n\r\nheader .nav-container .search-link {\r\n  margin: 11px 10px 0 0;\r\n  color: grey;\r\n  font-size: 15px;\r\n}\r\n\r\nheader .nav-container .right-nav .nav-search {\r\n  height: 40px;\r\n  width: 250px;\r\n  background-color: transparent;\r\n  border-radius: 20px;\r\n  border: 2px solid #333;\r\n  color: white;\r\n  padding-left: 10px;\r\n}\r\n\r\nheader .nav-container .right-nav .nav-search::placeholder {\r\n  color: grey;\r\n}\r\n\r\n\r\nul.suggestions {\r\n  list-style: none;\r\n  margin: 43px 0 0 0;\r\n  padding: 0;\r\n  position: absolute;\r\n  width: 250px;\r\n  border: none;\r\n  background-color: #171717;\r\n  max-height: 200px;\r\n  /* Optional: Limit height */\r\n  /* overflow-y: auto; */\r\n  z-index: 1;\r\n  display: none;\r\n  /* Hidden by default */\r\n}\r\n\r\nul.suggestions a {\r\n  text-decoration: none;\r\n  color: #fff;\r\n}\r\n\r\nul.suggestions li {\r\n  padding: 8px;\r\n  cursor: pointer;\r\n  font-size: 13px;\r\n  font-weight: bold;\r\n}\r\n\r\nul.suggestions li:hover {\r\n  box-shadow: 1px 1px 3px green;\r\n}\r\n\r\n\r\n/* NAVBAR SYLING ENDS ================================= */\r\n\r\n\r\n/* --------------------------------------------------------------------------------------------------- */\r\n\r\n\r\n/* HOMEPAGE BODY STYLING STARTS ========================= */\r\n/* WELCOME HEAD MESSAGE STARTS =========================*/\r\n.head.welcome-info {\r\n  text-align: center;\r\n  margin-top: 50px;\r\n}\r\n\r\n.head h1 {\r\n  font-size: 45px;\r\n  font-weight: 700;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.head .download-info {\r\n  font-size: 14px;\r\n  color: #ccc;\r\n  margin: 0 auto;\r\n  line-height: 22px;\r\n}\r\n\r\n.head .download-info a {\r\n  font-weight: 700;\r\n}\r\n\r\n.head a {\r\n  color: #428bca;\r\n}\r\n\r\n.head a:hover {\r\n  color: #428bcab7;\r\n}\r\n\r\n.head .links {\r\n  margin-top: 10px;\r\n}\r\n\r\n/* WELCOME HEAD MESSAGE ENDS ==========================*/\r\n\r\n\r\n/* -------------------------------------------------------------------------------------------------- */\r\n\r\n\r\n/* MOVIE LISTING STARTS ==============================*/\r\n.top-control-btn {\r\n  display: flex;\r\n  justify-content: center;\r\n  margin: 5px 0;\r\n}\r\n\r\n.top-control-btn button {\r\n  width: 70px;\r\n  height: 35px;\r\n  margin: 0 5px;\r\n  cursor: pointer;\r\n  background-color: transparent;\r\n  color: #fff;\r\n  border: 1px solid #777;\r\n}\r\n\r\n.top-control-btn .disabled {\r\n  opacity: 0.5;\r\n  cursor: default;\r\n}\r\n\r\n.container {\r\n  margin: 0 10%;\r\n  width: 80%;\r\n}\r\n\r\n.movie-listing .movie-row {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  justify-content: center;\r\n}\r\n\r\n.movie-listing figure {\r\n  padding-bottom: 0;\r\n  margin: 10px 20px;\r\n}\r\n\r\n.movie-listing figure a {\r\n  position: relative;\r\n  display: inline-block;\r\n  overflow: hidden;\r\n}\r\n\r\n.movie-listing figure .hover-text {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  color: white;\r\n  font-size: 25px;\r\n  font-weight: bold;\r\n  width: 220px;\r\n  height: 315px;\r\n  padding: 10px 20px;\r\n  border-radius: 5px;\r\n  text-align: center;\r\n  opacity: 0;\r\n  visibility: hidden;\r\n  transition: opacity 0.3s ease, visibility 0.3s ease;\r\n  display: flex;\r\n  justify-content: center;\r\n  flex-direction: column;\r\n}\r\n\r\n.movie-listing figure a:hover .hover-text {\r\n  opacity: 1;\r\n  visibility: visible;\r\n}\r\n\r\n.movie-listing figure .hover-rating {\r\n  margin-top: 0;\r\n}\r\n\r\n.movie-listing figure .hover-genre {\r\n  margin: 5px 0;\r\n}\r\n\r\n.movie-listing figure img {\r\n  width: 220px;\r\n  height: 315px;\r\n  border: 5px #fff solid;\r\n  border-radius: 5px;\r\n}\r\n\r\n.movie-listing figure a:hover img {\r\n  border: 5px #6ac045 solid;\r\n  